import { Component, OnInit } from '@angular/core';
import { GridsterConfig, GridsterItem } from 'angular-gridster2';
import { ClientsPanelComponent } from './clients-panel/clients-panel.component';
import { InvoicesPanelComponent } from './invoices-panel/invoices-panel.component';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  options: GridsterConfig;
  dashboard: Array<GridsterItem>;

  constructor() { }
  itemChange(item, itemComponent) {
  }
  itemResize(item, itemComponent) {
  }
  removeItem(event, item) {
   this.dashboard.splice(this.dashboard.indexOf(item), 1);
  }
  // addItem() {
  //  this.dashboard.push({});
  // }

  ngOnInit() {
    this.options = {
      itemChangeCallback: this.itemChange,
      itemResizeCallback: this.itemResize,
      minRows: 50,
      maxCols: 4,
      displayGrid: 'none',
      draggable: {
        enabled: true,
      },
     };
    this.dashboard = [
      {
        title: 'Clients',
        component: ClientsPanelComponent,
        cols: 2,
        rows: 20,
        y: 0,
        resizeEnabled: false,
        x: 2
      },
      {
        title: 'Invoices',
        component: InvoicesPanelComponent,
        cols: 2,
        rows: 28,
        resizeEnabled: false,
        y: 0,
        x: 0
      },
    ];
  }

}
